import Spot from '@/Spot/Spot.ts';
import { Bounds, Coordinate, MapJson } from '@/store/types';

export default class Map {

    private parentSpot: Spot | undefined = undefined;
    private spots: Spot[] = [];
    private _id: string | undefined;

    constructor(
        private id: number,
        private name: string,
        private bounds: Bounds,
        private floorName?: string,
        private description?: string,
    ) {
    }

    /**
     * mongodbid
     */
    public getDBId(): string | undefined {
        return this._id;
    }

    /**
     * id
     * @return id
     */
    public getId(): number {
        return this.id;
    }

    /**
     * 
     * @return 
     */
    public getName(): string {
        return this.name;
    }

    /**
     * 
     * @return 
     */
    public getSpots(): Spot[] {
        return this.spots;
    }

    /**
     * 
     * @return 
     */
    public getBounds(): Bounds {
        return this.bounds;
    }

    /**
     * 
     * @param bounds 
     * @return 
     */
    public getCenter(): Coordinate {
        const centerLat = (this.bounds.topL.lat + this.bounds.botR.lat) / 2;
        const centerLng = (this.bounds.topL.lng + this.bounds.botR.lng) / 2;
        return { lat: centerLat, lng: centerLng };
    }

    /**
     * description
     * @return descriptionundefined
     */
    public getDescription(): string | undefined {
        return this.description;
    }

    /**
     * 
     * @return undefined
     */
    public getParentSpot(): Spot | undefined {
        return this.parentSpot;
    }

    /**
     * @return 
     */
    public getAllDescendantSpots(): Spot[] {
        const spots: Spot[] = this.getSpots();
        return spots.concat(this.getSpots().map((spot) => {
            return spot.getDetailMaps()
                .map((map) => {
                    return map.getAllDescendantSpots();
                });
        }).flat(2));
    }

    /**
     * @return 
     */
    public getAllDescendantMaps(): Map[] {
        const maps: Map[] = [this];
        return maps.concat(this.getSpots().map((spot) => {
            return spot.getDetailMaps()
                .map((map) => map.getAllDescendantMaps());
        }).flat(2));
    }

    /**
     * spot,spotdetailMap.
     * @param parentSpot 
     */
    public setParentSpot(parentSpot: Spot): void {
        if (this.hasParentSpot(parentSpot)) {
            return;
        }
        this.parentSpot = parentSpot;
        parentSpot.addDetailMaps([this]);
    }

    /**
     * 
     * @param bounds 
     */
    public setBounds(bounds: Bounds): void {
        this.bounds = bounds;
    }

    /**
     * spot,spotparentMap.
     * .
     * @param spots spot
     */
    public addSpot(spot: Spot): void {
        if (this.hasSpot(spot)) {
            return;
        }
        this.spots.push(spot);
        spot.setParentMap(this);
    }

    /**
     * spot,spotparentMap.
     * .
     * @param spots spot
     */
    public addSpots(spots: Spot[]): void {
        for (const spot of spots) {
            this.addSpot(spot);
        }
    }

    /**
     * parentSpotspot
     * @param spot spot
     * @return spotparentSpottrue, false
     */
    public hasParentSpot(spot: Spot): boolean {
        return this.parentSpot === spot;
    }

    /**
     * spot
     * @param spot spot
     * @return true, false
     */
    public hasSpot(spot: Spot): boolean {
        return this.spots.includes(spot);
    }

    /**
     * floorName
     * @return 
     */
    public getFloorName(): string | undefined {
        return this.floorName;
    }

    /**
     * id
     * @param id id
     * @return null
     */
    public findSpot(id: number): Spot | null {
        for (const spot of this.spots) {
            const foundSpot: Spot | null = spot.findSpot(id);
            if (foundSpot !== null) {
                return foundSpot;
            }
        }
        return null;
    }

    /**
     * id
     * @param id id
     * @return null
     */
    public findMap(id: number): Map | null {
        if (this.id === id) {
            return this;
        }
        for (const spot of this.spots) {
            const foundMap: Map | null = spot.findMap(id);
            if (foundMap !== null) {
                return foundMap;
            }
        }
        return null;
    }

    /**
     * id
     * @param id id
     */
    public removeSpot(id: number): void {
        this.spots = this.spots.filter((spot) => spot.getId() !== id);
    }

    /**
     * id
     * @param newMapId id
     */
    public setId(newMapId: number) {
        this.id = newMapId;
    }

    /**
     * 
     * @param newName 
     */
    public setName(newName: string) {
        this.name = newName;
    }

    /**
     * description
     * @param newDescription description
     */
    public setDescription(newDescription: string) {
        this.description = newDescription;
    }

    /**
     * monogodbid
     * ()
     */
    public setDBId(_id: string) {
        this._id = _id;
    }

    /**
     * JSON.stringify
     * 
     * spotstoJSON
     * @return 
     */
    public toJSON(): MapJson {
        return {
            id: this.id,
            name: this.name,
            bounds: this.bounds,
            floorName: this.floorName,
            description: this.description,
            spots: this.spots.map((s: Spot) => s.toJSON()),
        };
    }

    /**
     * 
     * @param regExp 
     * @return bool
     */
    public isMatchToRegExp(regExp: RegExp): boolean {
       // RegExp.test(target:str)targetRegExptrue, false.
        return regExp.test(this.generateSearchTargetString());
    }

    /**
     * 
     * 
     * - 
     * - desctiption
     * 2
     * @return 
     */
    private generateSearchTargetString(): string {
        if (this.description === undefined) {
            return this.name;
        }
        return this.name + this.description;
    }
}
