import { Component, Vue, Prop } from 'vue-property-decorator';

@Component
export default class DeleteConfirmationDialog extends Vue {
    @Prop() public name!: string;

    /**
     * 
     */
    private confirmedDeletion(): void {
        this.$emit('del');
    }

    /**
     * 
     */
    private cancelDeletion(): void {
        this.$emit('cancel');
    }

}
